.contact {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  @media screen and (max-width: 600px) {
    flex-direction: column;
    div {
      width: 85%;
    }
  }
}

.send-message {
  flex-basis: 60%;
  display: flex;
  flex-direction: column;
  .title {
    margin-left: 25%;
    margin-bottom: 60px;
  }
  input {
    margin-left: 25%;
    width: 68%;
    transition: border 0.6s ease-in-out;
    &:focus {
      border-bottom: 1px solid #2f80ed;
    }
  }
  button {
    margin-left: 25%;
    margin-top: 105px;
  }
  @media screen and (max-width: 600px) {
    flex-basis: 50%;
    margin-bottom: 60px;
    justify-content: center;
    margin-left: 15%;
    .title {
      margin-left: 0;
    }
    input {
      margin-left: 0;
      width: 85%;
    }
    button {
      margin-top: 50px;
      margin-left: 0;
      align-self: center;
      margin-right: 15%;
    }
  }
}

.contact-info {
  display: flex;
  flex-direction: column;
  flex-basis: 40%;
  div,
  a {
    margin-left: 15%;
  }
  div {
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #334563;
    font-weight: 600;
    font-size: 12.6px;
    margin-top: 43px;
  }
  a {
    color: #56ccf2;
    letter-spacing: 2px;
    font-weight: 800;
    text-transform: uppercase;
    font-size: 24px;
    line-height: 150%;
    transition: opacity 0.3s ease;
    &:hover {
      opacity: 0.5;
    }
  }
  .title {
    margin-top: 0;
  }
  .footer-networks {
    margin-top: 55px;
    a {
      margin-left: 0;
      margin-right: 50px;
      z-index: 20;
      cursor: pointer;
      @media screen and (max-width: 1100px) {
        margin-right: 30px;
      }
      &:hover {
        opacity: 1;
      }
    }
    a:last-child {
      margin-right: 0;
    }
    img {
      z-index: 0;
      transition: all 0.2s linear;
      &:hover {
        transform: scale(1.2);
        -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
      }
    }
  }
}
